.stat {
  z-index: 4;
  position: relative;
  min-width: 1.1rem;
  max-width: 2rem;
  height: 1rem;
  margin: 0;
  margin-left: 4px;
  color: black;
  font-weight: bold;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  padding: 2px 1px 0px 2px;
  line-height: 1.2rem;
  font-size: 1rem;
}
.stat::after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 30%;
  height: 30%;
  left: 8px;
  top: 0.56rem;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background-color: #ffb4b4;
  box-shadow: #ffb4b4 0px 0px 5px 5px;
  opacity: 90%;
  transform: scale(1.3), translateX(-50%);
}
.icon {
  z-index: -1;
  position: absolute;
  /* width: 100%; */
  height: 1.2rem;
  left: 50%;
  transform: translateX(-50%);
  /* right: -0.1rem; */
  /* right: 3px; */
  top: 0.17rem;
  border-radius: 4px;
  /* opacity: 38%; */
  /* opacity: 90%; */
  overflow: hidden;
  font-size: 1.3rem;
}
.stat::before {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  /* height: 100%; */
  /* width: 90%; */
  height: 1.1rem;
  min-width: 1.1rem;
  /* left: 0; */
  right: 50%;
  transform: translateX(50%);
  /* right: -0.1rem; */
  top: 0.2rem;
  /* border-radius: 12px 19px 0px 17px; */
  border-radius: 0px 22px 22px 42px;
  border-radius: 3px;
  border-radius: 50%;
  box-shadow: inset #fccc2e 0px 0px 2px 1px;
  box-shadow: #3636368d 0px 0px 1px 1px, inset #ff6969 0px 0px 1px;
  background-color: #ff2200cb;
}
.random::before {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  height: 1rem;
  left: -1px;
  top: 3px;
  border-radius: 3px;
  border-radius: 0px 22px 22px 42px;
  background-color: #77c158;
  box-shadow: #3636368d 0px 0px 1px 1px, inset #baff8d 0px 0px 2px 1px;
  /* box-shadow: #3636368d 0px 0px 1px 1px, inset #a5ff6e 0px 0px 2px 2px; */
}
.full::before {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  height: 1rem;
  left: -1px;
  top: 3px;
  border-radius: 3px;
  background-color: #00ffd9;
  box-shadow: #9dfff58d 0px 0px 1px 1px, #8fffe1 0px 0px 4px 2px,
    inset #ecfffa 0px 0px 2px 3px, #3636368d 0px 0px 1px 1px;
}
.half::before {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  height: 1rem;
  left: -1px;
  top: 3px;
  /* border-radius: 12px 19px 0px 17px; */
  border-radius: 0px 22px 22px 42px;
  border-radius: 3px;
  box-shadow: #3636368d 0px 0px 1px 1px, inset #f4e98b 0px 0px 2px 1px;
  background-color: #d8b382;
}
.empty::before {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  height: 1rem;
  left: -1px;
  top: 3px;
  border-radius: 0px 22px 22px 42px;
  border-radius: 3px;
  box-shadow: inset #e24533 0px 0px 2px 1px;
  box-shadow: #3636368d 0px 0px 1px 1px, inset #ff9b85 0px 0px 2px 1px;
  background-color: #c37d75;
  /* background-color: #b06259; */
}
